
















import { Component, Vue, Prop } from 'vue-property-decorator'

@Component
export default class Tag extends Vue {

  @Prop({default: ''})
  prefixIcon?: string;

  @Prop({default: false})
  closable?: boolean;

  handleClose() {
    // 点击关闭按钮触发的事件 
    const tag = (this.$refs.neumorphicTagText as HTMLParagraphElement)
    this.$emit('close', tag);
  }
}
